// Solarized Dark // Altered for WCAG AA contrast
// For use with Pygments
// http://ethanschoonover.com/solarized
// SOLARIZED HEX      ROLE
// --------- -------- ------------------------------------------
// base03    #002b36  background
// base01    #819095  comments / secondary content
// base1     #93a1a1  body text / default code / primary content
// orange    #d86f57  constants
// red       #e86260  regex, special keywords
// blue      #3f92d6  reserved keywords
// cyan      #2aa198  strings, numbers
// green     #859900  operators, other keywords

pre,
code {
  font-family: $monospace-font-stack;
  // font-size: 1.15em;
  font-size: 80%;
}

code {
  background: #ddd;
  padding: 1px 5px;
}

pre {
  margin-top: 0;
  margin-bottom: 30px / 1.5;
  padding: 8px 12px;
  overflow-x: auto;
}

pre > code {
  border: 0;
  background: transparent;
  padding-right: 0;
  padding-left: 0;
}

.highlight { background-color: #002b36; color: #93a1a1; }
.highlight .c { color: #819095; } // Comment
.highlight .err { color: #93a1a1; } // Error
.highlight .g { color: #93a1a1; } // Generic
.highlight .k { color: #859900; } // Keyword
.highlight .l { color: #93a1a1; } // Literal
.highlight .n { color: #93a1a1; } // Name
.highlight .o { color: #859900; } // Operator
.highlight .x { color: #d86f57; } // Other
.highlight .p { color: #93a1a1; } // Punctuation
.highlight .cm { color: #819095; } // Comment.Multiline
.highlight .cp { color: #859900; } // Comment.Preproc
.highlight .c1 { color: #819095; } // Comment.Single
.highlight .cs { color: #859900; } // Comment.Special
.highlight .gd { color: #2aa198; } // Generic.Deleted
.highlight .ge { color: #93a1a1; font-style: italic; } // Generic.Emph
.highlight .gr { color: #e86260; } // Generic.Error
.highlight .gh { color: #d86f57; } // Generic.Heading
.highlight .gi { color: #859900; } // Generic.Inserted
.highlight .go { color: #93a1a1; } // Generic.Output
.highlight .gp { color: #93a1a1; } // Generic.Prompt
.highlight .gs { color: #93a1a1; font-weight: bold; } // Generic.Strong
.highlight .gu { color: #d86f57; } // Generic.Subheading
.highlight .gt { color: #93a1a1; } // Generic.Traceback
.highlight .kc { color: #d86f57; } // Keyword.Constant
.highlight .kd { color: #4798da; } // Keyword.Declaration
.highlight .kn { color: #859900; } // Keyword.Namespace
.highlight .kp { color: #859900; } // Keyword.Pseudo
.highlight .kr { color: #4798da; } // Keyword.Reserved
.highlight .kt { color: #e86260; } // Keyword.Type
.highlight .ld { color: #93a1a1; } // Literal.Date
.highlight .m { color: #2aa198; } // Literal.Number
.highlight .s { color: #2aa198; } // Literal.String
